const select = document.querySelector('select');
const allLang = ['en', 'ua'];
select.addEventListener('change', changeURLLanguage);

function changeURLLanguage() {
  let lang = select.value;
  localStorage.setItem('language', lang);
  location.href = window.location.pathname + '#' + lang;
  location.reload();
}

export function changeLanguage() {
  if (!localStorage.getItem('language')) {
    window.location.hash = 'en';
  }
  let hash = window.location.hash;

  hash = hash.substr(1);

  if (!allLang.includes(hash)) {
    location.href =
      window.location.pathname + '#' + localStorage.getItem('language');
    location.reload();
  }

  select.value = hash;

  for (let key in langArr) {
    let elem = document.querySelector('.lng-' + key);
    if (elem) {
      elem.innerHTML = langArr[key][hash];
    }
  }
}
changeLanguage();

function changeLang() {
  const input = document.querySelector('.search-form-input');

  if (!input) {
    return;
  }
  if (window.location.hash === '#ua') {
    input.placeholder = ' ';
  } else {
    input.placeholder = 'Movie search';
  }
}
changeLang();

function changeLangSing() {
  const title = document.querySelector('.login-title');
  if (window.location.hash === '#ua') {
    title === '';
  } else {
    title === 'Login';
  }
}
changeLangSing();

function changeTrailerBtnLang() {
  const gallery = document.querySelector('.gallery');

  if (window.location.hash === '#ua') {
    gallery.classList.remove('eng-gallery');
  } else {
    gallery.classList.add('eng-gallery');
  }
}
changeTrailerBtnLang();

function changeLangInputSingUp() {
  const input = document.querySelector('.input-form');
  const inputPass = document.querySelector('.password');
  const inputsingUp = document.querySelector('.input-form-singup');
  const inputsingUpEmail = document.querySelector('.input-email');
  const inputsingUpEmailConf = document.querySelector('.form-conf-pass');
  if (!input) {
    return;
  }
  if (window.location.hash === '#ua') {
    input.placeholder = ' ';
    inputPass.placeholder = '';
    inputsingUp.placeholder = '';
    inputsingUpEmail.placeholder = ' ';
    inputsingUpEmailConf.placeholder = ' ';
  } else {
    input.placeholder = 'Email';
    inputPass.placeholder = 'Password';
    inputsingUp.placeholder = 'password';
    inputsingUpEmail.placeholder = 'Email';
    inputsingUpEmailConf.placeholder = 'Confirm password';
  }
}
changeLangInputSingUp();
